    font-size: 22px;
    color: lightgray;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 2px;
}

.signin-cart:hover{
    text-decoration: none;
    color: white;
}

@media screen and (max-width: 960px){
    .top-header-container{
