/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: #232F3E;
}

.nav-item{
    padding: 15px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}



.top-divider{
    background-color: #93D600;
    height: 1.75rem;
    align-items: center;
    display: flex;
    font-size: 14px;    
    color: white;
    font-weight: 500;
    font-family: 'Roboto';
    letter-spacing: 1px;
}

.menu-item{
    text-align: left;
    cursor: pointer;
    padding: 1rem 1rem;
}

.menu-item:hover{
    background-color: #EAEDED;
}

.menu{
    position: fixed;
    overflow-y: scroll;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1;
    display: flex;
    flex-direction: column;
    grid-gap: 25px;
    gap: 25px;
    min-width: 280px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.side-bar-header-links {
    text-decoration: none;
    font-size: 15px;
    font-family: 'Roboto';
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: #191919;
}

.menu::-webkit-scrollbar{
    display: none;
}

.side-menu-profile-container{
    display: flex;
    justify-content: space-between;
    padding-bottom: 15px;
    background-color: #232F3E;
    padding: 1rem 1rem;
