@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.main-single-product-container{
    display: flex;
    flex-wrap: wrap;
    flex: 2;
    justify-content: space-evenly;
    padding: 2rem 2rem;
}

.single-product-image-container{
    display: flex;
    justify-content: center;
}

.single-product-image-card{
    border: none;
}

.single-product-view-image{
    width: 18rem;
    height: auto;
}

.single-product-view-image:hover{
    transition: all 0.2s ease-in-out;
    transform: scale(1.65);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.single-product-description-container{
    width: 400px;
    height: auto;
}

.single-product-description-container > ul > li {
    list-style-type : none;
    margin-bottom: 10px;
    text-align: justify;
    justify-content: center;
}

.single-product-description-card{
    width: 22rem;
    height: auto;
    border: none;
}

.single-product-view-title{
    font-size: 2rem;
    font-family: Arial, Helvetica, sans-serif;
}

.single-product-view-description{
    font-size: 0.95rem;
    font-family: Arial, Helvetica, sans-serif;
    color: #007600;
}

.single-product-view-price{
    font-size: 1.5rem;
    color: #B12704;
    font-weight: bold;
}

.single-product-view-availability{
    font-size: 1.2rem;
    color: #0081B0;
    font-family: 'Roboto';
}

.single-product-view-availability-badge{
    width: 2rem;
    height: 2rem;
    font-size: 1.2rem;
    font-family: 'Roboto';
}


.single-product-addtocart-card{
    width: auto;
    justify-content: center;
    text-align: center;
}

.single-product-view-rating{
    font-size: 1.2rem;
    color: #0081B0;
}

.single-product-view-numreviews{
    color: #E47911;
    font-size: 1.2rem;
}

.single-product-view-instock-status{
    font-size: 1.2rem;
    font-family: Arial, Helvetica, sans-serif;
}
.single-product-view-instock{
    color: #007600;
    font-size: 1.2rem;
    font-weight: bold;
}

.single-product-view-outofstock{
    color: red;
    font-size: 1.2rem;
    font-weight: bold;
}



.single-product-view-quantity-status{
    font-size: 1.2rem;
    font-family: 'Roboto';
}

.single-product-view-quantity{
    width: 5rem;
    height: 2rem;
    border: 1px solid black;
    cursor: pointer;
    padding: 5px;
}

.single-product-view-addtocart-btn{
    width: 15rem;
    height: 2.5rem;
    border: none;
    background-color: #f0c040;
    border: 1px solid #A88734;
    border-radius: 5px;
    font-size: 1rem;
    font-family: 'Roboto';
}


@media screen and (max-width: 750px){
    .main-single-product-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    
}