@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');

.main-cart-container{
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: space-evenly;
    margin: 1.5rem;
}

.empty-cart-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.empty-cart-status{
    font-size: 1.2rem;
    font-family: "Roboto";
    font-weight: bold;
}
.empty-cart-btn-container{
    display: flex;
    margin: 1.5rem;
}

.empty-cart-btn-container > button{
    border-radius: 2px;
    padding: 5px 15px;
    border: none;
    margin: 0.5rem;
    background-color: #F5F6F9;
    border: 1px solid #ADB1B8;
}

.cart-list-container{
    display: flex;
    width: 500px;
    justify-content: space-between;
    margin-bottom: 1rem;
    padding: 0.5rem;
    border-bottom: 1px solid lightgray;
}

.cart-list-product-image-card{
    border: none;
}

.cart-list-product-image{
    height: 8rem;
    width: 8rem;
    padding: 5px;
}

.cart-list-product-title{
    border: none;
    font-size: 1.25rem;
    font-family: 'Staatliches', cursive;
    letter-spacing: 2px;
}

.cart-list-product-title-link{
    color: #007185;
}

.cart-list-product-title-link:hover{
    color: #C7511F;
}

.cart-list-product-quantity{
    height: 2rem;
    width: 3rem;
    margin-top: 1.5rem;
}



.delete-item-icon{
    font-size: 1.5rem;
    color: crimson;
    cursor: pointer;
    
}

.cart-list-product-price{
    font-size: 1.2rem;
    margin-top: 1rem;
    color: #0F1111;
    border: none;
    font-weight: 500;
    font-family: 'Roboto';
}

.cart-total-container{
    width: 25%;
}

.cart-list-proceed-checkout-btn{
    height: 2.5rem;
    border: none;
    background-color: #F1C75F;
    border: 1px solid #A88734;
    border-radius: 5px;
    font-family: 'Roboto';
    font-size: 15px;
}

@media screen and (max-width: 960px){
    .main-cart-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .cart-list-container{
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        width: fit-content;
        align-items: center;
        justify-content: center;
    }
    .cart-list-product-image{
        height: 8rem;
    }
    .cart-total-container{
        width: 90%;
    }
}