@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.top-header-container{
    background-color: #1B262C;
    height: 5rem;
    display: flex;
    padding: 1rem 1rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.logo-container{
    display: flex;
    margin-left: 1rem;
}

.amazon-logo{
    height: 1.75rem;
    width: auto;
    margin-top: 0.5rem;
}

.main-search-bar{
    width: 500px;
    height: 2.5rem;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    padding: 10px;
    font-family: 'Roboto';
    border: 3px solid #FF9900;
}

.main-search-btn{
    width: auto;
    height: 2.5rem;
    border: none;
    background-color: #FEBD69;
    border-radius: 0 5px 5px 0;
    border: 2px solid #FF9900;
}

.main-search-btn-icon{
    font-size: 1.2rem;
}

.cart-icon{
    font-size: 1.75rem;
}

.signin-cart-container{
    display: flex;
}

.signin-cart-link-container:hover{
    text-decoration: none;
}

.signin-cart{
    margin: 1rem;
    font-size: 22px;
    color: lightgray;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 2px;
}

.signin-cart:hover{
    text-decoration: none;
    color: white;
}

@media screen and (max-width: 960px){
    .top-header-container{
        display: flex;
        gap: 1rem;
        height: auto;
        width: 100%;
        justify-content: center;

    }

    .search-bar-container{
        width: auto;
    }

    .main-search-bar{
        width: auto;
    }

    .signin-cart-container{       
        display: flex;

    }
}
.second-header-container{
    background-color: #232F3E;
    height: 3rem;
    /* position: sticky;
    top: 0;
    z-index: 1; */
    align-items: center;
    display: flex;
    justify-content: start;
}

.menu-bar-container{
    display: flex;
    justify-content: start;
    margin-left: 1rem;
    color: white;
    padding: 2px 6px;
}
.menu-bar{
    font-size: 1.5rem;
    color: white;
    cursor: pointer;
}

.top-nav-data-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: 1.5rem;
}

@media screen and (max-width: 960px){
    .second-header-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        height: auto;
    }
    
    .top-nav-data-container{
        display: flex;
        flex-wrap: wrap;
    }
}

.nav-links{
    text-decoration: none;
    padding: 10px;
    color: white;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
}

.nav-links:hover{
    text-decoration: none;
    color: lightgray;
}


/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: #232F3E;
}

.nav-item{
    padding: 15px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}



.top-divider{
    background-color: #93D600;
    height: 1.75rem;
    align-items: center;
    display: flex;
    font-size: 14px;    
    color: white;
    font-weight: 500;
    font-family: 'Roboto';
    letter-spacing: 1px;
}
